import React, { useEffect, useState } from "react";

import { HeadFC, HeadProps, PageProps, useStaticQuery } from "gatsby";
import { graphql } from "gatsby";

import {
  IServicesPageContext,
  IServicesPreviewQueryResult,
} from "../types/services.type";

import Banner from "../components/common/banner";
import ContentContainer from "../components/common/contentContainer";
import ServicePreview from "../components/services/servicePreview";
import StackSection from "../components/stack/stackSection";
import { HeadSeo } from "../components/common/metadata";

const Services = ({
  data,
  pageContext: { markupData },
}: PageProps<IServicesPreviewQueryResult, IServicesPageContext>) => {
  useEffect(() => {
    if (window && "hbspt" in window)
      // @ts-ignore
      window.hbspt.forms.create({
        portalId: "21836033",
        formId: "3eae609c-bcf2-47f4-ad93-11a65c4b4f9d",
        target: "#formtarget",
      });
  }, []);

  const services = data.allMarkdownRemark?.nodes;

  return (
    <>
      <Banner heightClass="h-136" title={markupData.bannerTitle} />
      <StackSection />
      <div className="bg-main-100">
        <ContentContainer className="flex-col items-center p-6 md:p-12">
          <div className="columns-1 md:columns-2 w-full py-14">
            {services
              ? services.map((service) => (
                  <ServicePreview service={service} key={service.id} />
                ))
              : null}
          </div>
        </ContentContainer>
      </div>
      <div className={`bg-secondary-100`}>
        <ContentContainer className="flex-col items-center p-6 md:p-12">
          <div id="formtarget" className="" />
        </ContentContainer>
      </div>
    </>
  );
};

export default Services;

export const Head: HeadFC<
  IServicesPreviewQueryResult,
  IServicesPageContext
> = ({
  pageContext: { markupData, site, lang },
  location,
}: HeadProps<IServicesPreviewQueryResult, IServicesPageContext>) => (
  <>
    <HeadSeo
      title={`${markupData.head} | ${markupData.bannerTitle.toUpperCase()}`}
      siteUrl={site.siteUrl}
      path={location.pathname}
      lang={lang}
    />
    <script
      charSet="utf-8"
      type="text/javascript"
      src="//js.hsforms.net/forms/embed/v2.js"
    ></script>
  </>
);

export const query = graphql`
  query ($lang: String) {
    allMarkdownRemark(
      filter: {
        fields: { sourceName: { eq: "services" }, language: { eq: $lang } }
      }
    ) {
      nodes {
        id
        html
        frontmatter {
          title
          image {
            publicURL
          }
        }
      }
    }
  }
`;
